import { Navigation } from "@/components/navigation"
import { Footer } from "@/components/footer"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { FileText, Target, TrendingUp, CheckCircle2, ArrowRight, Sparkles, Heart } from "lucide-react"
import Link from "next/link"
import { useEffect, useRef, useState } from "react"

function LifeScriptProcessSection() {
  const steps = [
    {
      number: "01",
      title: "Genetic & Microbiome Sampling",
      description: "Simple at-home collection of your DNA and gut microbiome samples",
    },
    {
      number: "02",
      title: "Epigenetic Lifestyle Audit",
      description: "Comprehensive analysis of your habits, environment, and behaviors",
    },
    {
      number: "03",
      title: "Data Analysis & Interpretation",
      description: "Advanced laboratory analysis of your biological markers",
    },
    {
      number: "04",
      title: "Personal LifeScript Session",
      description: "One-on-one consultation to understand your personalized blueprint",
    },
    {
      number: "05",
      title: "30-Day Follow-Up",
      description: "Alignment check and protocol adjustments based on your progress",
    },
  ]

  const [activeIndex, setActiveIndex] = useState(0)
  const sectionRef = useRef<HTMLElement>(null)
  const stepRefs = useRef<(HTMLDivElement | null)[]>([])

  useEffect(() => {
    const observerOptions = {
      root: null,
      rootMargin: "-30% 0px -30% 0px",
      threshold: 0.3,
    }

    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          const index = stepRefs.current.findIndex((ref) => ref === entry.target)
          if (index !== -1) {
            setActiveIndex(index)
          }
        }
      })
    }, observerOptions)

    stepRefs.current.forEach((ref) => {
      if (ref) observer.observe(ref)
    })

    return () => {
      stepRefs.current.forEach((ref) => {
        if (ref) observer.unobserve(ref)
      })
    }
  }, [])

  return (
    <section ref={sectionRef} className="py-24 px-6 lg:px-8 bg-white min-h-screen">
      <div className="container mx-auto max-w-7xl relative">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 lg:gap-16">
          {/* THE LIFESCRIPT PROCESS Heading - Left Side (Sticky) */}
          <div className="lg:sticky lg:top-32 lg:self-start lg:h-screen lg:flex lg:flex-col lg:justify-start lg:pt-8">
            <div className="max-w-2xl">
              <h2 className="font-serif text-4xl sm:text-5xl md:text-6xl lg:text-7xl text-gray-900 font-bold leading-tight mb-8">
                The LifeScript Process
              </h2>

              {/* Progress Indicators - Dots */}
              <div className="flex items-center gap-3 mt-8">
                {steps.map((_, index: number) => (
                  <div
                    key={index}
                    className={`transition-all duration-300 rounded-full ${index === activeIndex
                      ? "w-3 h-3 bg-gray-900"
                      : index < activeIndex
                        ? "w-2 h-2 bg-gray-400"
                        : "w-2 h-2 bg-gray-300"
                      }`}
                  />
                ))}
              </div>
            </div>
          </div>

          {/* All Steps - Right Side (Scrollable) */}
          <div className="space-y-8 lg:space-y-12 lg:max-w-2xl pb-24">
            {steps.map((step, index: number) => (
              <div
                key={index}
                ref={(el) => {
                  stepRefs.current[index] = el
                }}
                id={`step-${index}`}
                className="scroll-mt-32"
              >
                <div className="flex items-start gap-6 p-6 rounded-lg bg-white border border-gray-200 shadow-sm hover:shadow-md transition-shadow">
                  {/* Number Badge */}
                  <div className="flex-shrink-0">
                    <div className="w-16 h-16 rounded-full bg-teal-100 flex items-center justify-center">
                      <span className="text-2xl font-bold text-teal-700 font-mono">{step.number}</span>
                    </div>
                  </div>

                  {/* Content */}
                  <div className="flex-1">
                    <h3 className="font-serif text-xl sm:text-2xl text-gray-900 font-semibold mb-2">
                      {step.title}
                    </h3>
                    <p className="text-base text-gray-600 leading-relaxed">
                      {step.description}
                    </p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  )
}

export default function LifeScriptPage() {
  const features = [
    {
      icon: FileText,
      title: "Comprehensive Health Assessment",
      description: "Complete evaluation of your current health status and biological markers",
    },
    {
      icon: Target,
      title: "Personalized Health Plan",
      description: "Custom roadmap designed specifically for your biology and goals",
    },
    {
      icon: TrendingUp,
      title: "Progress Tracking",
      description: "Continuous monitoring and adjustment of your health optimization journey",
    },
    {
      icon: Heart,
      title: "Longevity Focus",
      description: "Evidence-based protocols to extend healthspan and improve quality of life",
    },
  ]

  const planComponents = [
    "DNA and genetic analysis",
    "Comprehensive biomarker testing",
    "Nutritional optimization protocols",
    "Exercise and movement prescriptions",
    "Sleep optimization strategies",
    "Stress management techniques",
    "Supplement recommendations",
    "Lifestyle modification guidance",
  ]

  const benefits = [
    {
      phase: "Initial",
      duration: "Weeks 1-4",
      results: [
        "Baseline health assessment",
        "Personalized plan development",
        "Initial protocol implementation",
        "First progress measurements",
      ],
    },
    {
      phase: "Optimization",
      duration: "Months 2-6",
      results: [
        "Improved biomarker profiles",
        "Enhanced energy and vitality",
        "Better sleep and recovery",
        "Increased cognitive performance",
      ],
    },
    {
      phase: "Long-term",
      duration: "Months 6+",
      results: [
        "Sustained health improvements",
        "Reduced biological age markers",
        "Optimized longevity protocols",
        "Peak performance maintenance",
      ],
    },
  ]

  return (
    <main className="min-h-screen">
      <Navigation />

      {/* Hero Section */}
      <section className="pt-32 pb-16 px-6 lg:px-8 bg-gradient-to-b from-background to-secondary/20">
        <div className="container mx-auto max-w-4xl text-center">
          <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-primary/10 text-primary text-sm font-medium mb-6">
            <FileText className="w-4 h-4" />
            Personalized Health Plan
          </div>
          <h1 className="font-serif text-4xl sm:text-5xl md:text-6xl text-foreground mb-6">LifeScript</h1>
          <p className="font-serif text-xl sm:text-2xl text-accent italic mb-8">
            Your Personalized Longevity Plan
          </p>
          <p className="text-lg sm:text-xl text-muted-foreground leading-relaxed max-w-3xl mx-auto mb-8">
            A comprehensive, data-driven approach to optimizing your health, extending your healthspan, and maximizing your quality of life through personalized protocols and continuous monitoring.
          </p>
          <Link href="/assessment">
            <Button size="lg" className="px-8">
              Start Your LifeScript
              <ArrowRight className="ml-2 h-5 w-5" />
            </Button>
          </Link>
        </div>
      </section>

      {/* What is LifeScript */}
      <section className="py-16 px-6 lg:px-8">
        <div className="container mx-auto max-w-4xl">
          <h2 className="font-serif text-3xl sm:text-4xl text-foreground mb-6 text-center">
            Your Personalized Path to Optimal Health
          </h2>
          <div className="prose prose-lg max-w-none mb-12">
            <p className="text-muted-foreground leading-relaxed mb-4">
              LifeScript is a comprehensive health optimization program that creates a personalized roadmap for your longevity journey. Based on your unique biology, genetics, and lifestyle, we develop a detailed plan that addresses every aspect of your health.
            </p>
            <p className="text-muted-foreground leading-relaxed">
              From nutrition and exercise to sleep and stress management, LifeScript provides you with the tools, insights, and support needed to achieve and maintain optimal health throughout your life.
            </p>
          </div>

          <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
            {features.map((feature, index) => (
              <Card key={index} className="border-2">
                <CardHeader>
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center flex-shrink-0">
                      <feature.icon className="w-5 h-5 text-primary" />
                    </div>
                    <CardTitle className="font-serif text-lg">{feature.title}</CardTitle>
                  </div>
                </CardHeader>
                <CardContent>
                  <p className="text-sm text-muted-foreground">{feature.description}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </section>

      {/* LifeScript Components */}
      <section className="py-16 px-6 lg:px-8 bg-secondary/20">
        <div className="container mx-auto max-w-4xl">
          <div className="text-center mb-12">
            <h2 className="font-serif text-3xl sm:text-4xl text-foreground mb-4">What's Included</h2>
            <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
              A comprehensive approach to health optimization
            </p>
          </div>

          <Card className="border-2">
            <CardContent className="pt-8">
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                {planComponents.map((component, index) => (
                  <div key={index} className="flex items-start gap-3">
                    <CheckCircle2 className="w-5 h-5 text-primary flex-shrink-0 mt-0.5" />
                    <span className="text-muted-foreground">{component}</span>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>
      </section>

      {/* The LifeScript Process */}
      <LifeScriptProcessSection />

      {/* Benefits Timeline */}
      <section className="py-16 px-6 lg:px-8">
        <div className="container mx-auto max-w-6xl">
          <div className="text-center mb-12">
            <h2 className="font-serif text-3xl sm:text-4xl text-foreground mb-4">Your Journey to Optimal Health</h2>
            <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
              Progressive improvements as you follow your personalized LifeScript
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {benefits.map((benefit, index) => (
              <Card key={index} className="border-2">
                <CardHeader>
                  <CardTitle className="font-serif text-2xl">{benefit.phase}</CardTitle>
                  <CardDescription className="text-accent font-medium">{benefit.duration}</CardDescription>
                </CardHeader>
                <CardContent>
                  <ul className="space-y-2">
                    {benefit.results.map((result, i) => (
                      <li key={i} className="flex items-start gap-2 text-sm text-muted-foreground">
                        <CheckCircle2 className="w-4 h-4 text-primary flex-shrink-0 mt-0.5" />
                        <span>{result}</span>
                      </li>
                    ))}
                  </ul>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-16 px-6 lg:px-8">
        <div className="container mx-auto max-w-3xl text-center">
          <h2 className="font-serif text-3xl sm:text-4xl text-foreground mb-6">Ready to Start Your LifeScript?</h2>
          <p className="text-lg text-muted-foreground mb-8">
            Begin your personalized health optimization journey with our comprehensive assessment.
          </p>
          <div className="flex flex-col sm:flex-row items-center justify-center gap-4">
            <Link href="/assessment">
              <Button size="lg" className="px-8">
                Start Assessment
                <ArrowRight className="ml-2 h-5 w-5" />
              </Button>
            </Link>
            <Link href="/contact">
              <Button size="lg" variant="outline" className="px-8 bg-transparent">
                Book Consultation
              </Button>
            </Link>
          </div>
          <p className="text-sm text-muted-foreground mt-6">Duration: 6-12 months | Investment: Custom pricing</p>
        </div>
      </section>

      <Footer />
    </main>
  )
}

